import { Success } from ".."
import { mock } from '../request';

mock.onGet("/user/selectList", { id: 1 }).reply((req: any) => {
  const { name } = JSON.parse(req.data || "{}")
  const data = [
    { id: 1, name: "" },
    { id: 2, name: "" },
    { id: 3, name: "" },
  ]

  return [
    200,
    Success({
      data: name ? data.filter(item => item.name.indexOf(name) > -1) : data
    })
  ]
})

