<template>
  <a-button type="primary" @click="request()"></a-button>
  <p>{{ data }}</p>
</template>
<script lang="ts" setup>
import { message } from "ant-design-vue";
import { createAxios } from "howuse/axios";

const { useAxiosRequest } = createAxios({
  baseURL: "//www.httpbin.org",
});

const { data, response, loading, finished, execute, edata } = useAxiosRequest<any>(
  {
    url: "/anything",
    method: "post",
  },
  {
    defaultVal: {}, //  []
    isDebounce: true
  }
);

// 
watch(finished, (finished)=> finished && message.info(""))


// 
function request() {
  execute().then((data) => {
    message.info(``);
  });
  execute().then((data) => {
    message.info(``);
  });
  execute().then((data) => {
    message.info(``);
  });
}
</script>
