import Mock from "mockjs"
import { Success } from ".."
import { mock } from "../request"

mock.onGet("/area/city1").reply(200, Success({
  data: [
    { id: 1, name: "" },
    { id: 2, name: "" },
    { id: 3, name: "" },
  ]
}))

const obj = {
  "": [
    { id: 31, name: "" },
    { id: 32, name: "" },
    { id: 33, name: "" },
  ],
  "": [
    { id: 21, name: "" },
    { id: 22, name: "" },
    { id: 23, name: "" },
  ],
  "": [
    { id: 11, name: "" },
    { id: 12, name: "" },
    { id: 13, name: "" },
    { id: 14, name: "" },
    { id: 15, name: "" },
    { id: 16, name: "" },
  ]
}

mock.onGet("/area/city2").reply((req: any) => {
  const { name } = JSON.parse(req.data || "{}")

  if (!name) {
    return Success({
      data: []
    })
  }
  const obj = {
    "": [
      { id: 1, name: "" },
      { id: 2, name: "" },
      { id: 3, name: "" },
    ],
    "": [
      { id: 1, name: "" },
      { id: 2, name: "" },
      { id: 3, name: "" },
    ],
    "": [
      { id: 1, name: "" },
      { id: 2, name: "" },
      { id: 3, name: "" },
      { id: 4, name: "" },
      { id: 5, name: "" },
      { id: 6, name: "" },
    ]
  }

  return [
    200,
    Success({
      data: obj[name]
    })
  ]
})



mock.onGet("/area/city-tree").reply(200, Success({
  data: [
    { id: 1, name: "", children: obj[""] },
    { id: 2, name: "", children: obj[""] },
    { id: 3, name: "", children: obj[""] },
  ]
}))


